* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  //font-family: 'Dancing Script', cursive;
  font-family: 'Montserrat', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-image: linear-gradient(315deg, #7ab342 0%, #6523ae);
}

.wrapper {
  display: flex;
  flex-direction: column;
  width: 1000px;
  height: 600px;
}

.info {
  text-align: center;
  font-size: 30px;

  h1 {
    margin-bottom: 10px;
    font-size: 60px;
  }
}

.ball-img {
  position: relative;
  height: 250px;

  img {
    position: absolute;
    top: 30px;
    left: 300px;
    width: 300px;
    cursor: pointer;
    transform: translate(-50%);
  }
}

.question-area {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 24px;

  h2 {
    margin-bottom: 10px;
    font-size: 36px;
    text-transform: uppercase;
  }

  input {
    padding: 10px 20px;
    width: 80%;
    font-size: 20px;
    border: 2px solid #333;
    border-radius: 8px;
  }

  .answer {
    margin-top: 30px;


    span {
      font-weight: bold;
    }
  }

  .error {
    font-weight: bold;
    color: #3add8c;
  }
}

.shake-animation {
  animation: shake 1s 1;
}

@keyframes shake {
  0% {
    left: 290px;
  }
  20% {
    left: 340px;
  }
  40% {
    left: 290px;
  }
  60% {
    left: 340px;
  }
  80% {
    left: 290px;
  }
  100% {
    left: 300px;
  }
}